import {
  GET_ORDER_REQUEST,
  GET_ORDER_SUCCESS,
  GET_ORDER_FAILED,
  CLEAR_ORDER,
} from "../actions/order";

import { IOrderState, IOrderAction, IOrder } from "../../types/order-types";

const initialState = {
  orderRequest: false,
  orderFailed: false,
  order: undefined,
};

export const orderReducer = (
  state: IOrderState = initialState,
  action: IOrderAction
) => {
  switch (action.type) {
    case GET_ORDER_REQUEST: {
      return {
        ...state,
        //   
        orderRequest: true,
        //       
        //  ,       
        orderFailed: false,
      };
    }
    case GET_ORDER_SUCCESS: {
      return {
        ...state,
        //   ,     
        order: getOrderData(action.order),
        //    
        orderRequest: false,
      };
    }
    case GET_ORDER_FAILED: {
      return {
        ...state,
        //    ,
        //     
        orderFailed: true,
        //    
        orderRequest: false,
        order: getFailOrderData(),
      };
    }
    case CLEAR_ORDER: {
      return initialState;
    }
    default: {
      return state;
    }
  }
};

const getOrderData = (order: IOrder | undefined) : IOrder => {
  if (!order) {
    return getFailOrderData();
  }

  return {
    number: order.number,
    status: {
      value: 1,
      text: "   ",
      description: "    ",
    },
  };
};

const getFailOrderData = (): IOrder => {
  return {
    number: "",
    status: {
      value: 2,
      text: "    ",
      description: "  ",
    },
  };
};
