import {
  GET_FEEDORDER_REQUEST,
  GET_FEEDORDER_SUCCESS,
  GET_FEEDORDER_FAILED,
  CLEAR_FEEDORDER,
} from "../../actions/feed/feedOrder";

import {
  IFeedOrderState,
  IFeedOrderAction,
} from "../../../types/feed-types";

const initialState = {
  orderRequest: false,
  orderFailed: false,
  order: undefined,
};

export const feedOrderReducer = (
  state: IFeedOrderState = initialState,
  action: IFeedOrderAction
) => {
  switch (action.type) {
    case GET_FEEDORDER_REQUEST: {
      return {
        ...state,
        //   
        orderRequest: true,
        //       
        //  ,       
        orderFailed: false,
      };
    }
    case GET_FEEDORDER_SUCCESS: {
      return {
        ...state,
        //   ,     
        order: action.order,
        //    
        orderRequest: false,
      };
    }
    case GET_FEEDORDER_FAILED: {
      return {
        ...state,
        //    ,
        //     
        orderFailed: true,
        //    
        orderRequest: false,
        order: action.order,
      };
    }
    case CLEAR_FEEDORDER: {
      return initialState;
    }
    default: {
      return state;
    }
  }
};
